import React from 'react';
import Bundle from './bundle';

//  null 
const Loading = () => <div>Loading...</div>;

/*
 *	
 *	 props 
 */ 
const lazyLoad = loadComponent => props => (
	<Bundle load={loadComponent}>
		{Comp => (Comp ? <Comp {...props} /> : <Loading />)}
  	</Bundle>
);

export default lazyLoad;


